body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

h1 {
  /* color: #daad86; */
  color: red;
  font-weight: 800;
  font-size: 2rem;
  margin-bottom: 40px;
}

span {
  color: #312c32;
  font-weight: 600;
}

form {
  margin-bottom: 40px;
}

input {
  height: 30px;
  width: 200px;
  margin: 0;
  padding-left: 10px;
  border-color: 1px solid #312c32;
  color: #312c32;
}

input.search {
  margin-top: 10px;
  width: 285px;
}

input:focus {
  outline: none;
}

button {
  height: 36px;
  width: 75px;
  border: none;
  margin-left: 10px;
  background-color: #98dafc;
  color: white;
  cursor: pointer;
}

button:hover {
  border: 1px solid #98dafc;
  background-color: white;
  color: #98dafc;
}

button:focus {
  outline: none;
}

ul {
  padding: 0;  
  list-style: none;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center; 
  -webkit-align-items: center; 
          align-items: center;
}

li {
  margin: 5px 0;
  width: 299px;
  height: 36px;
  /* background-color: #daad86; */
  background: linear-gradient(to right, red, orange, yellow, lime, blue, purple, fuchsia);
  font-weight: bold;
  color: white;
  line-height: 36px;
}

li:hover {
  /* background-color: white; */
  /* color: #daad86; */
  /* border: 1px solid #daad86; */
  cursor: pointer;

  border: 1px solid white;
  color: white;
  font-weight: bold;
  background: linear-gradient(to left, red, orange, yellow, lime, blue, purple, fuchsia);
}

